
.container {
    height: 100%;
    display:flex;
    justify-content: center;
}




.loader {
    width:100%;
    height:100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight:300;
    font-size:30px;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items:center;
    flex-wrap: wrap;
    padding:30px;
    padding-top:70px;
    font-weight:300;
    width:80%;
}

